@import '../_vars.scss';

.container {
  display: flex;
  // min-height: 100%;
  flex: 1;
  &.direction-column {
    flex-direction: column;
  }
  &.direction-row {
    flex-direction: row;
  }
  &.center {
    justify-content: center;
    align-items: center;
  }
  &.end {
    align-items: flex-end;
  }
  &.bgBlue {
    background-color: map-get($m-colors, "bg-primary");
  }
}

.title {
  background-color: map-get($m-colors, "primary");
  color: #fff;
  border-radius: 0px 0px 47px 0px;
  h1 {
    color: #fff;
  }
}

.box {
  background-color: #fff;
  box-shadow: 3px 4px 10px rgba(70, 79, 108, 0.27);
  border-radius: 19px;
  margin: 40px;
  padding: 20px;
  flex: 1;
  display: flex;
  &.direction-column {
    flex-direction: column;
  }
  &.direction-row {
    flex-direction: row;
  }
}
.icon {
  display: flex;
  flex: 1;
  flex-direction: column;
  align-items: center;
  margin: 20px;
  text-decoration: none;
  div {
    display: flex;
    padding: 20px;
    width: 150px;
    height: 150px;
    justify-content: center;
    background-color: #fff;
    border-radius: 19px;
    box-shadow: 3px 4px 10px rgba(70, 79, 108, 0.27);
    margin-bottom: 20px;
    transition: .2s ease-out;
  }
  span {
    text-transform: uppercase;
  }
}
.icon:hover {
  div {
    box-shadow: 3px 4px 10px rgba(70, 79, 108, 0.5);
    transition: .1s ease-in;
  }
}

.counter {
  flex: 1;
  align-items: center;
  display: flex;
  text-align: center;
  flex-direction: column;
  div {
    align-self: center;
    font-size: 20px;
    border: 5px solid #fff;
    box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.13), 0px 0px 0px 1px #D4D4D4 inset;
    width: 78px;
    height: 78px;
    text-align: center;
    justify-content: center;
    align-items: center;
    display: flex;
    border-radius: 60px;
    margin-top: 10px;
    margin-bottom: 15px;
  }
  .couter-title {
    margin-bottom: 0;
    display: flex;
    min-height: 45px;
    align-items: center;
  }
}

.badge {
  border-radius: 25px;
  font-size: 14px;
  color: #fff;
  text-align: center;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: 20px;
  text-transform: capitalize;
  &.badge-red {
    background-color: #E60000;
  }
  &.badge-orange {
    background-color: #F97700;
  }
  &.badge-yellow {
    background-color: #F6BC27;
  }
  &.badge-green {
    background-color: #6EAF1B;
  }
}
.states {
  text-align: center;
  width: 320px;
  margin: auto;
  align-items: center;
  position: relative;
  .states-action {
    border-radius: 100rem;
    margin: $m-space auto 0;
    max-width: 320px;
    width: 320px;
    background-color: map-get($m-colors, "primary");
  }
  .states-options {
    background: #fff;
    border: 1px solid #efefef;
    border-radius: 3px 3px 0 0;
    box-shadow: 0 4px 10px rgba(0,0,0,.15);
    width: 320px;
    height: 27vh;
    overflow: scroll;
    position: absolute;
    top: 90px;
    .states-option {
      background: #fff;
      display: block;
      line-height: 3em;
      text-decoration: none;
    }
  }
}


@media (max-width: 920px) {
  #codeandocovid > .container {
    flex-direction: column;
  }
  .mobileColumn {
    flex-direction: column!important;
  }
  .movileHide {
    display: none;
  }
  .title {
    border-radius: 0px;
  }
}
